import React from 'react';
import { navigateTo } from "gatsby-link"
import './YellowButtonLG.css';

const YellowButtonLG = (props) => 
{
    return (
        <button 
            className = "YellowButtonLG" 
            role = "button"
            onClick = {() => navigateTo(props.path)}
        >
        {props.children}
        </button>
    );
}

export default YellowButtonLG;


