import React from 'react';
import { navigateTo } from 'gatsby-link';
import './RedButtonLG.css';

const RedButtonLG = props => {
    return (
        <button 
            className = "RedButtonLG" 
            role = "button"
            onClick = {() => navigateTo(props.path)}
        >
        {props.children}
        </button>
    );
}

export default RedButtonLG;


